<script>
// @ is an alias to /src
export default {
  name: 'home',
  components: {
  },
  watch: {
    '$route' (to, from) {
       GoToCalc();
    }
  },
  computed: {
    FurnitureList () {
      return this.$store.getters.FurnitureList;
    },
    SelectedFurniture () {
      return this.$store.getters.FurnitureList.filter(fur => {
                if (fur.selected === true) {
                    return fur.id;
                }
            })[0];
    }
  },
  methods: {
    GoTo (id) {
      switch (id) {
        case 1:
          this.$router.push({name: 'repairbed', params: { scr: '1' } });
          break;
        case 2:
          this.$router.push({name: 'repairchairbed', params: { scr: '1' } });
          break;
        case 3:
          this.$router.push({name: 'repairchair', params: { scr: '1' } });
          break;
        case 4:
          this.$router.push({name: 'repairofficechair', params: { scr: '1' } });
          break;
        case 5:
          this.$router.push({name: 'repairofficestool', params: { scr: '1' } });
          break;
        case 6:
          this.$router.push({name: 'repairstool', params: { scr: '1' } });
          break;
        case 7:
          this.$router.push({name: 'repairkitchenarea', params: { scr: '1' } });
          break;
        case 8:
          this.$router.push({name: 'repairsofa', params: { scr: '1' } });
          break;
