      if (id === undefined) {
        if (this.SelectedFurniture === undefined) {
           alert("Для начала вам нужно выбрать мебель из списка");
        } else {
          this.$store.dispatch('SAVE_FURNITURE', SelectedFurniture);
        }
      }
      this.$store.dispatch('SAVE_FURNITURE', id);
      this.GoTo(id);
    },
    GoToBtn () {
      if (this.SelectedFurniture) {
        this.GoTo(this.SelectedFurniture.id)
      } else {
        alert('Для начала вам нужно выбрать мебель для ремонта');
