import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Bed from '../views/Bed.vue'
import ChairBed from '../views/ChairBed.vue'
import OfficeChair from '../views/OfficeChair.vue'
import Couch from '../views/Couch.vue'
import CornerSofa from '../views/CornerSofa.vue'
import KitchenArea from '../views/KitchenArea.vue'
import Sofa from '../views/Sofa.vue'
import Chair from '../views/Chair.vue'
import OfficeStool from '../views/OfficeStool.vue'
import Stool from '../views/Stool.vue'
import Puf from '../views/Puf.vue'
import Sofa2Corner from '../views/Sofa2Corner.vue'
import Bed2Couch from '../views/Bed2Couch.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'home',
        component: Home
    },
    {
        path: '/repairbed/:scr',
        name: 'repairbed',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: Bed,
        props: true,
    },
    {
        path: '/repairchairbed/:scr',
        name: 'repairchairbed',
        component: ChairBed,
        props: true,
    },
    {
        path: '/repairofficechair/:scr',
        name: 'repairofficechair',
        component: OfficeChair,
        props: true,
    },
    {
        path: '/repaircouch/:scr',
        name: 'repaircouch',
        component: Couch,
        props: true,
    },
    {
        path: '/repairsofa/:scr',
        name: 'repairsofa',
        component: Sofa,
        props: true,
    },
    {
        path: '/repaircornersofa/:scr',
        name: 'repaircornersofa',
        component: CornerSofa,
        props: true,
    },
    {
        path: '/repairkitchenarea/:scr',
        name: 'repairkitchenarea',
        component: KitchenArea,
        props: true,
    },
    {
        path: '/repairchair/:scr',
        name: 'repairchair',
        component: Chair,
        props: true,
    },
    {
        path: '/repairofficestool/:scr',
        name: 'repairofficestool',
        component: OfficeStool,
        props: true,
    },
    {
        path: '/repairstool/:scr',
        name: 'repairstool',
        component: Stool,
        props: true,
    },
    {
        path: '/repairpuf/:scr',
        name: 'repairpuf',
        component: Puf,
        props: true,
    }, //
    {
        path: '/sofa2corner/:scr',
        name: 'sofa2corner',
        component: Sofa2Corner,
        props: true,
    },
    {
        path: '/bed2couch/:scr',
        name: 'bed2couch',
        component: Bed2Couch,
        props: true,
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router